export default {
	data() {
		return {
			PageCur: 'user',
			animation: 'scale-up',
		};
	},
	methods: {
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur;
			this.$emit('navigate', {
				page: this.PageCur
			})
		},
		Toggle(e) {
			var anmiaton = e.currentTarget.dataset.class;
			this.animation = anmiaton;
			setTimeout(() => {
				this.animation = '';
			}, 400)
		},
	}
}
